import React from 'react';
import {Link} from 'react-router-dom';


const Detaildisplay=(props)=>{

    console.log(props)
    const renderTable=({restdata})=>{
        if(restdata){
         return restdata.map((item)=>{
           // console.log(item)
            return(
                <tr>
                    <td><Link to={`/rest/${item._id}`}>{item.name}</Link></td>
                   
                    <td>{item.locality}</td>
                    <td>{item.Cuisine[0].name},{item.Cuisine[1].name}</td>
                    <td>{item.type[0].name},{item.type[1].name}</td>
                    <td>{item.cost}</td>
                    
                </tr>
            )
         })
        } 
    }
    return(
        <div className='container'>
            <center><h3>resturant list</h3></center>
            <table className="table">
                <thead>
                    <tr>
                    <th>name</th>
                    
                    <th>locality</th>
                    <th>Cuisine</th>
                    <th>MEalType</th>
                    <th>cost</th>

                    </tr>
                </thead>
              
             <tbody>
             {renderTable(props)}
             </tbody>
            
            </table>
        </div>
    )
}

export default Detaildisplay;


