import React,{Component} from 'react';
import {Link} from 'react-router-dom';



const cityurl="http://localhost:8900/city";
const resturl="http://localhost:8900/resturanthome?city=";
class Search extends Component{
    constructor(props){
        super(props)
        this.state={
            
            location:"",
            City:'',
            resturant:""
        }
    }
     
     handleCity=(event)=>{
         this.setState({resturant:""})
         this.setState({city:event.target.value})
         var cityid=parseInt(event.target.value)
         sessionStorage.setItem('cityid',event.target.value)
         console.log(`${resturl}${cityid}`)
         fetch(`${resturl}${cityid}`,{method:'GET'})
         .then((res)=>res.json())
         .then((data)=>{
             this.setState({resturant:data})
         })

     }

     renderDatacity=(result)=>{
           if(result){
               return result.map((item)=>{
                   return(
                       <option value={item.city}>
                         {item.name}
                       </option>
                   )
               })
           }
     }
     renderrest=(data)=>{
         if(data){
             return data.map((item)=>{
                 return(
                     <option value={item._id}>
                        {item.name} | {item.locality}
                     </option>
                 )
             })
         }
     }

     handleResturant=(event)=>{
         this.props.restid(`${Number(event.target.value)}`)
     }

    render(){
        return(
           
                <header>
                    <center>
                        
                        <select class="locationdropdown" onChange={this.handleCity}>
                           {this.renderDatacity(this.state.location)}
                        </select>
                    &nbsp;
                    &nbsp;
                     
                    {/* <input list="resturant"name='resets'id='resets'/>*/}
                     <select class="resturantinput"  id='resturant' onChange={this.handleResturant}>
                       { /*<input type="text" placeholder="search for resturants"/>*/}
                         {this.renderrest(this.state.resturant)}
                     </select>
                    </center>
                    
                </header>
                
               
        )
    }
    componentDidMount(){
        fetch(cityurl,{method:'GET'})
        .then((res)=>res.json())
        .then((data)=>{
           this.setState({location:data})
        })
    }
    
}

export default Search;










